import { NgModule, Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { NgbModule, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NgxMaskModule } from '../shared/ngx-mask';
import { environment } from '../../environments/environment.prod';
import { Status } from './model/Pesquisa';
import { Router } from '@angular/router';
import { Service } from './services/service';
import { Alert } from 'selenium-webdriver';
import { ModalAndamento } from './modal-andamento/modal-andamento.components';
import { ModalAtividade } from './modal-atividade/modal-atividade.components';

@Component({
  selector: 'app-toDo',
  templateUrl: './lista.component.html',
  styleUrls: ['./lista.component.css']
})
export class ToDoComponent implements OnInit {

  lista: [] = [];
 
  page = 0;
  pageSize = 50;
  collectionSize = 0;
  total = 0;
  carregando = false;
  url_base = `${environment.API}`;
  alerts: any;
  subscription = null;
  listaAssuntos: any = [{ codigo: '0', descricao: '' }, { codigo: '5', descricao: 'Administrativo' }, { codigo: '1', descricao: 'Cobrança' },{ codigo: '2', descricao: 'Compras' },{ codigo: '3', descricao: 'Comunicacao' },{ codigo: '5', descricao: 'Contabilidade' }, { codigo: '4', descricao: 'Contratos' },  { codigo: '6', descricao: 'Fiscalização' }, { codigo: '7', descricao: 'Financeiro' }, { codigo: '8', descricao: 'Gestão de Pessoas' },{ codigo: '9', descricao: 'Informática' }, { codigo: '10', descricao: 'Jurídico' }, { codigo: '11', descricao: 'Presidência' }, { codigo: '12', descricao: 'Registro' }];
  listaStatus: any = [{ codigo: '0', descricao: 'Pendente' }, { codigo: '2', descricao: 'Concluído' }, { codigo: '3', descricao: 'Cancelado' }];
  status: Status;
  queryBusca: String;

  constructor(
    private router: Router,
    public service: Service,
    private modalService: NgbModal
  ) { }

  ngOnInit() {
        
    this.carregando = true;
    this.subscription = this.service.getListaByFiltro( "2000-01-01", "3999-12-31", "", "0")    
      .subscribe(
        response => {
          console.log("Status" + response.status);
          if (response.status.codigo == 0) {
            this.lista = response.dados;
          } else {
            this.lista = [];
            this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
          }
          this.carregando = false;
        },
        err => {
          this.carregando = false;
          this.alerts = Array.from([{ type: 'danger', message: err.message }])
          console.log(err);
        });



  }

  novo() {
    this.router.navigate(['/estudante', 'novo']);
  }

  novaAtividade() {

    const modalRef = this.modalService.open(ModalAtividade,  { size: 'lg' });
    console.log('Antes');
    console.log('Depois');   

  }   


//  openModal(formPai) {
//    this.subscription = this.service.get(formPai.id)
//      .subscribe(
//        response => {
//          var currentObject = response.dados[0];
//          const modalRef = this.modalService.open(ModalAtividade, { size: 'lg' });
//          modalRef.componentInstance.formPai = response.dados;
//        });
//  }

  openModalAndamento(formPai) {
          const modalRef = this.modalService.open(ModalAndamento, { size: 'lg' });
          modalRef.componentInstance.formPai = formPai;
  }


  pesquisar() {
    this.alerts = [];
    this.carregando = true;
    this.subscription = this.service.getBusca(this.queryBusca)
      .subscribe(
        response => {
          console.log("Status" + response.status);
          if (response.status.codigo == 0) {
            this.lista = response.dados;
          } else {
            this.lista = [];
            this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
          }
          this.carregando = false;
        },
        err => {
          this.carregando = false;
          this.alerts = Array.from([{ type: 'danger', message: err.message }])
          console.log(err);
        });

    this.collectionSize = this.lista.length;

  }
  filtro(){

    const elemAssunto: HTMLInputElement =<HTMLInputElement>document.getElementById('comboFiltroAssunto');
    const elemStatus: HTMLInputElement =<HTMLInputElement>document.getElementById('comboFiltroStatus');
    const elemDtInicio: HTMLInputElement =<HTMLInputElement>document.getElementById('filtroDtInicio');
    const elemDtFim: HTMLInputElement =<HTMLInputElement>document.getElementById('filtroDtFim');

    var dataInicio = "2000-01-01";
    if (elemDtInicio.value !== ""){
      var dtTemp = elemDtInicio.value;
      dataInicio = dtTemp.substring(6,10) + "-" + dtTemp.substring(3,5) +"-"+  dtTemp.substring(0,2);
    }

    var dataFim = "2099-12-31";
    if (elemDtFim.value !== ""){
      var dtTemp = elemDtFim.value;
      dataFim = dtTemp.substring(6,10) + "-" + dtTemp.substring(3,5) +"-"+  dtTemp.substring(0,2);
    }


    this.subscription = this.service.getListaByFiltro( dataInicio, dataFim, elemAssunto.value, elemStatus.value)
    .subscribe(
      response => {
        console.log("Status" + response.status);
        if (response.status.codigo == 0) {
          this.lista = response.dados;
        } else {
          this.lista = [];
          this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
        }
        this.carregando = false;
      },
      err => {
        this.carregando = false;
        this.alerts = Array.from([{ type: 'danger', message: err.message }])
        console.log(err);
      });
    this
  }

  close(alert: Alert) {
    this.alerts.splice(this.alerts.indexOf(alert), 1);
  }

  ngOnDestroy() {
    if (this.subscription != null) {
      this.subscription.unsubscribe();
    }
  }

  listarAnexos(id, a){
    var htmlRetorno = "<ul>";
    var listaAnexos = a.split("|");
    for(var i =0; i < listaAnexos.length; i++){
      if (listaAnexos[i] != ""){
        htmlRetorno = htmlRetorno + "<ul><li><a href='https://todo.crago.org.br/" + id +"-"+ listaAnexos[i] + "' target='blank'>"+ listaAnexos[i] +"</a></li></ul>"  ;
      }
    }
    htmlRetorno = htmlRetorno + "</ul>";
    return htmlRetorno;    
}

  exportToExcel(event) {
    this.service.exportAsExcelFile(this.lista, 'toDo');
}

}