import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { FaleConoscoGuard } from '../guards/faleConosco.guard';
import { ToDoComponent } from './toDo.component';
import { ModalAtividade } from './modal-atividade/modal-atividade.components';

const moduloRoutes = [
    {path: '', component: ToDoComponent, 
     canActivateChild: [FaleConoscoGuard],
     children : [
        {path: 'novo', component: ModalAtividade}
    ]}
];
@NgModule({
    imports: [RouterModule.forChild(moduloRoutes)],
    exports: [RouterModule]
})
export class RoutingModule {}

