import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgxMaskModule } from '../shared/ngx-mask';
import { RoutingModule } from './toDo.routing.module';

import { ModalAndamento } from './modal-andamento/modal-andamento.components';
import { ToDoComponent } from './toDo.component';
import { ModalAtividade } from './modal-atividade/modal-atividade.components';
import { FileUploadModule } from 'ng2-file-upload';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RoutingModule,
    ReactiveFormsModule,
    FileUploadModule,     
    SharedModule,
    NgbModule,
    NgxMaskModule.forRoot(),
  ],
  declarations: [ToDoComponent, ModalAtividade, ModalAndamento],
  entryComponents: [ ModalAtividade, ModalAndamento ]
  
  
})
export class ToDoModule { }



