import { Alert } from 'selenium-webdriver';
import { Cidade } from './../../shared/models/cidade';
import { EstadoBr } from './../../shared/models/estado-br.model';
import { DropdownService } from './../../shared/services/dropdown.service';
import { ConsultaCepService } from './../../shared/services/consulta-cep.service';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Pais } from './../../shared/models/pais';
import { cepValidator, getLabelAllFields, getFormValidationErrors } from '../../shared/form-validation';
import { map, tap, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { empty, Observable } from 'rxjs';
import { Service } from '../services/service';
import { FileUploader } from 'ng2-file-upload';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';

const URL_UPLOAD = `${environment.API}toDo/uploadFile.php`;

@Component({
    selector: 'app-modal-atividade',
    templateUrl: 'modal-atividade.html',
    styleUrls: ['modal-atividade.css']
})
export class ModalAtividade implements OnInit {
    @Input() public formPai;
    @Output() passEntry: EventEmitter<any> = new EventEmitter();

    form: FormGroup;
    uploadForm: FormGroup;

    submitted = false;
    alerts = [];

    carregando = false;
    listaAssuntos: any = [{ codigo: '5', descricao: 'Administrativo' }, { codigo: '1', descricao: 'Cobrança' },{ codigo: '2', descricao: 'Compras' },{ codigo: '3', descricao: 'Comunicacao' },{ codigo: '4', descricao: 'Contabilidade' },  { codigo: '6', descricao: 'Escritório Jurídico' }, { codigo: '7', descricao: 'Fiscalização' }, { codigo: '8', descricao: 'Financeiro' }, { codigo: '9', descricao: 'Gestão de Pessoas' },{ codigo: '10', descricao: 'Informática' }, { codigo: '11', descricao: 'Jurídico' }, { codigo: '12', descricao: 'Presidência' }, { codigo: '13', descricao: 'Registro' }, { codigo: '13', descricao: 'Superintendencia' }];
    listaTemas: any = [{ codigo: '1', descricao: 'Assinatura/SEI' },{ codigo: '2', descricao: 'Atendimento' },{ codigo: '3', descricao: 'Compra de produto' },{ codigo: '5', descricao: 'Contratação de serviço' }, { codigo: '4', descricao: 'Estudo' },  { codigo: '6', descricao: 'Levantamento de dados' }, { codigo: '7', descricao: 'Manifestação juridica' }, { codigo: '8', descricao: 'Minuta de portaria/resolução/oficio' },{ codigo: '9', descricao: 'Pagamentos' }, { codigo: '10', descricao: 'Parecer juridico' }, { codigo: '11', descricao: 'Processos judiciais' }, { codigo: '12', descricao: 'Relatorio' }, { codigo: '13', descricao: 'Solicitação diversas'}];
    modoLeitura = false;

    public uploader: FileUploader = new FileUploader({ url: URL_UPLOAD });
    
    constructor(
        public activeModal: NgbActiveModal,
        private fb: FormBuilder,
        private service: Service,
        private cepService: ConsultaCepService,
        private dropdownService: DropdownService,
        private http: HttpClient

    ) { }

    ngOnInit() {

        this.form = this.fb.group({

            assunto: [null],
            tema:[null],
            descricao: [null],
            prazo: [null],
            usuario: [null],
            nomeAnexos: [null]
          
        });

        this.uploadForm = this.fb.group({
          document: [null, null],
          type: [null, Validators.compose([Validators.required])]
      });       


        var jsonUsuario = JSON.parse(localStorage.getItem("usuario"));
        this.form.get('usuario').setValue(jsonUsuario.nome);        
        
    }

    sair(){
        this.activeModal.close('FECHAR');
    }

    populaDadosForm(dados) {
        // this.formulario.setValue({});

    }

    close(alert: Alert) {
        this.alerts.splice(this.alerts.indexOf(alert), 1);
    }

    getLabelField(fieldName: string) {
        return getLabelAllFields(fieldName);
    }

    salvar() {
        console.log('Salvando');
        this.alerts = [];
        this.submitted = true;
    
        // stop here if form is invalid
        if (this.form.invalid) {
   
          this.alerts = getFormValidationErrors(this.form.controls);
    
          console.log('Formulário inválido');
          return;
        }

        var nomeAnexos = "";
        for (let i = 0; i < this.uploader.queue.length; i++) {
          let fileItem = this.uploader.queue[i]._file;
          const el: HTMLInputElement = <HTMLInputElement>document.getElementById("uploadArquivos");
          const anexoArquivos = el.value;
          if (anexoArquivos.indexOf(fileItem.name) > -1) {
            nomeAnexos = nomeAnexos + fileItem.name + "|";
          }
        }

        this.form.get('nomeAnexos').setValue(nomeAnexos);


        this.carregando = true;

        this.service.salvar(this.form)
          .subscribe(
            response => {
              console.log("Status" + response.status);          
              if (response.status.codigo == 0){
                this.alerts = Array.from([{ type: 'success', message: response.status.mensagem }]);
                this.uploadFiles(response.id);
              }else{
                this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
              }
            this.carregando = false;
            },
            err => {
              this.carregando = false;
              this.alerts = Array.from([{ type: 'danger', message: err.message }])          
              console.log(err);
            });
    }


    uploadFiles(id: string ) {
      for (let i = 0; i < this.uploader.queue.length; i++) {
          let fileItem = this.uploader.queue[i]._file;
          if (fileItem.size > 10000000) {
              alert("Each File should be less than 10 MB of size.");
              return;
          }
      }
      for (let j = 0; j < this.uploader.queue.length; j++) {
          let tipo = "";
          let data = new FormData();
          let fileItem = this.uploader.queue[j]._file;

          console.info(this.uploader.queue[j]);
          console.log(fileItem.name);
          data.append('file', fileItem);
          data.append('id', id);
          data.append('fileName', fileItem.name);
          data.append('fileSeq', 'seq' + j);
          data.append('dataType', this.uploadForm.controls.type.value);
          this.uploadFile(data).subscribe(data => console.log(data.status.mensagem));
      }
//        this.uploader.clearQueue();
  }

  uploadFile(data: FormData): Observable<any> {
    return this.http.post(URL_UPLOAD, data);
  }
  
  listarAnexos(id, a){
    var htmlRetorno = "<ul>";
    var listaAnexos = a.split("|");
    for(var i =0; i < listaAnexos.length; i++){
      if (listaAnexos[i] != ""){
        htmlRetorno = htmlRetorno + "<ul><li><a href='https://anexo.crago.org.br/" + id +"-"+ listaAnexos[i] + "' target='blank'>"+ listaAnexos[i] +"</a></li></ul>"  ;
      }
    }
    htmlRetorno = htmlRetorno + "</ul>";
    return htmlRetorno;    
}

}